.Nav {
    position        : fixed;
    bottom          : 0;
    opacity         : 10;
    background-color: #FFFFFF;
    box-shadow      : 0 0 10px 1px #4444441f;
}

.Navitems {
    width          : 100vw;
    display        : flex;
    justify-content: space-around;
    align-items    : center;
}

.Navitem {
    width          : 25%;
    height         : 10vh;
    display        : flex;
    justify-content: center;
    align-items    : center;
    list-style-type: none;
}